import { Component } from '@angular/core';
import { NavController, ViewController } from 'ionic-angular';
import { NavParams } from 'ionic-angular';


@Component({
  selector: 'page-update-item',
  templateUrl: 'update-item.html'
})
export class UpdateItemPage {
  id;
  title;
  description;
  duetime;



  constructor(public navCtrl: NavController, public view: ViewController, public navParams: NavParams) {
  
  

  }

  ionViewDidLoad() {
    this.id = this.navParams.get('item').id;
    this.title = this.navParams.get('item').title;
    this.description = this.navParams.get('item').description;
    this.duetime = this.navParams.get('item').duetime;
  }

  updateItem() {

    let updateItem = {
      id: this.id,
      title: this.title,
      description: this.description,
      duetime: this.duetime,
      status: "P"
    };

    this.view.dismiss(updateItem);

  }

 

  close() {
    this.view.dismiss();
  }

}




